import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute } from '@angular/router';


import { VehicleService } from 'src/app/api/controllers/Vehicle';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SessionService } from 'src/app/core/session/session.service';

import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-add-vehicle',
  templateUrl: './add-vehicle.component.html',
  styleUrls: ['./add-vehicle.component.scss']
})
export class AddVehicleComponent implements OnInit {

  vehicle:any;
  isEditVehicle:boolean = false;

  isVehicleSubmitted:boolean = false;
  isVehicleAdded:boolean = false;

  isError:boolean = false;
  errorMessage:string = "";

  vehicleTypeData:any;
  vehicleTypeId:string = "";

  apartmentBlockUnitId:any;


  @Output() public outputParams = new EventEmitter();

  constructor(
    private route: ActivatedRoute,
    private vehicleService: VehicleService,
    private lookupService: LookupService,
    private sessionService:SessionService

  ) { }


 
  

  submitAddVehicleForm(form) {

  	this.isVehicleSubmitted = true;

  	if(!this.isEditVehicle) {

  		let details = {
  
		  "vehicleNumber": this.vehicle.vehicleNumber,
		  "vehicleTypeId": parseInt(this.vehicleTypeId),
		  "vehicleManufacturer": this.vehicle.vehicleManufacturer,
		  "vehicleModel": this.vehicle.vehicleModel,
		  "vehicleDriverNumber": this.vehicle.vehicleDriverNumber,
		  "vehicleColor": this.vehicle.vehicleColor,
		  "isActive": true,
		  "insertedBy": parseInt(this.sessionService.userId),
		  "insertedOn":  _moment().toISOString(),
		  "updatedBy": null,
		  "updatedOn": null

		}

		let params = {
			vehicle:details
		}

		this.vehicleService.addVehicle(params).subscribe((res : any) => {

			if(res.message) {
	           
	           var vehicleId = res.message;

	           let mapVehicleDetails = {

				  "apartmentBlockUnitId": this.apartmentBlockUnitId,
				  "vehicleId": vehicleId,
				  "userId": parseInt(this.sessionService.userId),
				  "documentId": 0,
				  "parkingSlotId": parseInt(this.route.params['value'].slotid),
				  "stickerNumber": "string",
				  "rfid": "string",
				  "fromDate": _moment().toISOString(),
				  "toDate": _moment().toISOString(),
				  "isPassIssued": true,
				  "isActive": true,
				  "insertedBy": parseInt(this.sessionService.userId),
				  "insertedOn": _moment().toISOString(),
				  "updatedBy": null,
				  "updatedOn": null

			   }
			   
			   let params = {
				apartmentBlockUnitVehicle: mapVehicleDetails
			   }

	           this.vehicleService.addApartmentBlockUnitVehicle(params).subscribe((res : any) => {

	           	if(res.message) {
	           		this.isVehicleAdded= true;
	           	}

	           });


	         }
	         else {

	          this.isVehicleSubmitted = false;
	          this.isVehicleAdded = false;
	          this.isError = true;
	          this.errorMessage = res.errorMessage;

	         }
 

		}, error => {
	        
	    });

  	}

  	else {

  		let details = {
  		  
  		  "vehicleId": parseInt(this.route.params['value'].id),
		  "vehicleNumber": this.vehicle.vehicleNumber,
		  "vehicleTypeId": parseInt(this.vehicleTypeId),
		  "vehicleManufacturer": this.vehicle.vehicleManufacturer,
		  "vehicleModel": this.vehicle.vehicleModel,
		  "vehicleDriverNumber": this.vehicle.vehicleDriverNumber,
		  "vehicleColor": this.vehicle.vehicleColor,
		  "isActive": this.vehicle.isActive,
		  "insertedBy": this.vehicle.insertedBy,
		  "insertedOn": this.vehicle.insertedOn,
		  "updatedBy": parseInt(this.sessionService.userId),
		  "updatedOn": _moment().toISOString()

		}

		let params = {
			vehicle:details
		}

		this.vehicleService.updateVehicle(params).subscribe((res : any) => {

			if(res.message) {
				this.isVehicleAdded = true;
			}
			else {

			  this.isVehicleSubmitted = false;
	          this.isVehicleAdded = false;
	          this.isError = true;
	          this.errorMessage = res.errorMessage;

			}

		});

  	}

	

  }


  ngOnInit() {

  	this.vehicle = {};

	let params = {
		LookupTypeId: 2,
		ApartmentId: this.sessionService.apartmentId
	}

  	//get vehicle type
  	this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res : any) => {

  		this.vehicleTypeData = res;

  	});

  	if(this.route.params['value'].id != undefined) {

  		this.isEditVehicle = true;

  		this.vehicleService.getVehicleByVehicleId(this.route.params['value'].id).subscribe((res : any) => {

  			this.vehicle = res[0];
  			this.vehicleTypeId = res[0].vehicleTypeId.toString();

  		});

  	}

  	if(this.route.params['value'].slotid != undefined) {

  		this.vehicleService.getParkingSlotById(this.route.params['value'].slotid).subscribe((res : any) => {

  			this.apartmentBlockUnitId = res[0].apartmentBlockUnitId;
  		});

  	}
    
  }

}
