import { Component, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import { debounceTime, distinctUntilChanged, tap, filter, map, switchMap} from 'rxjs/operators';
import { ParkingService } from 'src/app/api/controllers/Parking';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { Router, ActivatedRoute } from '@angular/router';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { StaffService } from 'src/app/api/controllers/Staff';

import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-parking-create-parking-booking',
  templateUrl: './parking-create-parking-booking.component.html',
  styleUrls: ['./parking-create-parking-booking.component.scss']
})
export class ParkingCreateParkingBookingComponent implements OnInit {
  slotSubject = new Subject<string>();
  slotData$:any;
  parkingAvailable:any = {};
  parkingSlotList:any;
  slotRateList = [];
  statusList = [];
  towerList = [];
  unitList = [];
  autoSearchData:any= {};
  staffsList= {
    "dropdownList":[],
    "selectedItems":[]
  };
  staffsdropdownSettings:any = {};
  type:string;

  timeZone:any;

  constructor( private parkingService: ParkingService, private sessionService:SessionService,
    private sharedService: SharedService,private router : Router,
    private lookupService : LookupService,
    private apartmentService:ApartmentService,
    private staffService: StaffService,
    private activateRouter: ActivatedRoute) { }

  onSelectSlot(event) {
    let selectedValue = event.option.value;
    this.autoSearchData.displaySlotNumber =  `${selectedValue.parkingSlotNo} location ${selectedValue.slotTypeName}(${selectedValue.permOwner} ${selectedValue.apartmentBlockNumber} ${selectedValue.apartmentBlockUnitNumber})`;
    this.autoSearchData = selectedValue;
    this.parkingAvailable.availabelParkingId = selectedValue.parkingSlotID;   //parkingId
  }

  submitParkingSlot() {
    let entity = {
      "id": 0,
      "availabelParkingId": this.parkingAvailable.availabelParkingId,
      "rentedParkingAllocationTypeId": this.parkingAvailable.rentedParkingAllocationTypeId,
      "startDate": this.parkingAvailable.startDate,
      "endDate":  this.parkingAvailable.endDate,
      "startTime": this.parkingAvailable.startTime,
      "endTime": this.parkingAvailable.endTime,
      "totalFee": this.parkingAvailable.totalFee,
      "isCancelled": true,
      "refundAmount": 0,
      "isActive": true,
      "insertedBy": parseInt(this.sessionService.userId),
      "insertedOn": _moment().toISOString(),
      "updatedBy": null,
      "updatedOn": null,
      "visitorId": 0,
      "apartmentBlockUnitId": this.parkingAvailable.apartmentBlockUnitId,
      "currentParkingTrackerId": null,
      "addToMonthyBill": true,
      "parkingBookingStatusId":  this.parkingAvailable.parkingBookingStatusId,
      "staffId": this.type == 'unit-community' ? this.staffsList.selectedItems[0].staffId : null,
    }
    let parking = {
      allocParking : entity
    }
    this.parkingService.allocateParking(parking).subscribe((res:any)=>{
      if(!isNaN(res.message)) {
        this.sharedService.setAlertMessage("Parking Slot Created successfully");
        if(this.type == 'unit-unit') 
          this.router.navigate(['/ams/parking-management/manage-allocate-availability-list/unit-to-unit-allocation'])
        else if(this.type == 'unit-community')
          this.router.navigate(['/ams/parking-management/manage-allocate-availability-list/unit-to-community'])
        else 
          this.router.navigate(['/ams/parking-management/manage-allocate-availability-list/community-to-unit'])
      } else if(res.message) {
        this.sharedService.setAlertMessage(res.message);
      } else {
        this.sharedService.setAlertMessage(res.errorMessage);
      }
    })
  }

  getUnits(type) {
    let unitParams = {
      apartmentBlockId: this.parkingAvailable.tempBlockId
    }
    this.apartmentService.getApartmentBlockUnitByBlockId(unitParams).subscribe((res:any) => {
      this.unitList = res;
    },
    error => {
      console.log(error);
    });
  }

  selectFees() {
    for(let data of this.slotRateList) {
      if(data.id == this.parkingAvailable.rentedParkingAllocationTypeId) {
        this.parkingAvailable.totalFee = data.fee
        break;
      }
    }
  }


  ngOnInit() {

    this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

    this.activateRouter.params.subscribe(param=>this.type = param['type']);
    this.slotData$ = this.slotSubject.pipe(
      debounceTime(300),
      distinctUntilChanged(),
      filter(data=>data.length > 2),
      switchMap(term=>{
        let entity = {
          ApartmentID: this.sessionService.apartmentId,
          KeywordSlotNo: term,
          SlotMainTypeId : this.type == 'community-unit' ? 300 : 299,  //communityOwned = 300 and UnitOwned = 299
        }
        return this.parkingService.getParkingSlotNoByKeyword(entity)
    }))
    this.slotData$.subscribe(res=>{
      if(res.length > 0) {
        this.parkingSlotList = res;
      }
    })

    //Slot Rate Type List
    let getParam = {
      ApartmantId:this.sessionService.apartmentId
    }
    this.parkingService.getParkingAllocationType(getParam).subscribe((resp:any) =>{
      this.slotRateList = resp.filter(data => {
        return data.isActive
      });
    },error =>{
      console.log(error)
    })

    //Status
    let slotTypeParams = {
      LookupTypeId: 97,
      ApartmentId: this.sessionService.apartmentId
    }
    this.lookupService.getLookupValueByLookupTypeId(slotTypeParams).subscribe((res: any) => {
      if(res.length > 0) {
        this.statusList = res.filter((data)=>{
          if(data.lookupValueId == 65 || data.lookupValueId== 390)
            return data;
          else if(this.type == 'unit-community' && (data.lookupValueId == 61 || data.lookupValueId== 392 || data.lookupValueId== 393))
            return data;
          else if((this.type == 'unit-unit' || this.type == 'community-unit') && data.lookupValueId== 391)
            return data;
        })
      }
    })
    //tower
    let params = {
      apartmentId: this.sessionService.apartmentId
    }
    this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res:any) => {
      this.towerList = res;
    });

    if(this.type == 'unit-community') {
      this.staffsdropdownSettings={
        singleSelection: true,
        primaryKey: 'staffId',
        labelKey: 'customLabel',
        enableSearchFilter: true,
        enableFilterSelectAll: false,
        autoPosition: false,
        maxHeight:130
      };
      let staffParms = {
        apartmentId: this.sessionService.apartmentId,
        roleTypeIds: this.sessionService.roleTypeId
      }
      this.staffService.getAllStaffs(staffParms).subscribe((res:any) => {
        if(res.length) {
          this.staffsList.dropdownList = res.filter((ele)=>{
            ele.customLabel = `${ele.firstName} ${ele.lastName} ${ele.staffCategory_Label}`
            return ele.isActive
          });
        }
      },error => {
        console.log(error)
      });
    }
  }
}
