import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { VehicleService } from 'src/app/api/controllers/Vehicle';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';
import { StaffService } from 'src/app/api/controllers/Staff';

import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-allocated-slot-confirmation',
  templateUrl: './allocated-slot-confirmation.component.html',
  styleUrls: ['./allocated-slot-confirmation.component.scss']
})
export class AllocatedSlotConfirmationComponent implements OnInit {
  allocateSlotData:any = {};
  blockUnit = [];
  towerList = [];
  towerId:any;
  staffsList:any= [];
  constructor(public dialogRef: MatDialogRef<AllocatedSlotConfirmationComponent>,
    private apartmentService:ApartmentService,
    private vehicleService : VehicleService,
    private sharedService: SharedService,
    private staffService: StaffService,
    private sessionService:SessionService,
    @Inject(MAT_DIALOG_DATA) public data:any
  ) {}

  getUnits(type) {
    if(type =='change') {
      this.allocateSlotData.apartmentBlockUnitId ='';
    }
    let unitParams = {
      apartmentBlockId: this.towerId
    }
    this.apartmentService.getApartmentBlockUnitByBlockId(unitParams).subscribe((res:any) => {
      this.blockUnit = res;
    },
    error => {
      console.log(error);
    });
  }

  setStaff(event) {
    this.allocateSlotData.staffId = event[0].staffId;
  }

  slotAllocation() {
    let entity = {
      apartmentId: this.allocateSlotData.apartmentId,
      parkingSlotBlockNumber: this.allocateSlotData.parkingSlotBlockNumber,
      apartmentBlockUnitId: this.allocateSlotData.apartmentBlockUnitId,
      comment: this.allocateSlotData.comment,
      isActive: true,
      parkingSlotId: this.allocateSlotData.parkingSlotId,
      parkingTypeId: this.allocateSlotData.parkingTypeId,
      permSlotStatusId: parseInt(this.allocateSlotData.permSlotStatusId),
      slotMainTypeId: this.allocateSlotData.slotMainTypeId,
      slotName: this.allocateSlotData.slotName,
      slotStatusId: this.allocateSlotData.slotStatusId,
      slotTypeId: this.allocateSlotData.slotTypeId,
      sqrFt: this.allocateSlotData.sqrFt,
      insertedBy: this.allocateSlotData.insertedBy,
      insertedOn: this.allocateSlotData.insertedOn,
      tempApartmentBlockUnitUserId : null,
      staffId : this.allocateSlotData.slotMainTypeId == 300 ? this.allocateSlotData.staffId : null,
      updatedBy: parseInt(this.sessionService.userId),
      updatedOn: _moment().toISOString()
    }
    let parkingSlot = {
      parkingSlot : entity
    }
    this.vehicleService.updateParkingSlot(parkingSlot).subscribe(resp => {
      this.sharedService.setAlertMessage("Parking Slot Updated successfully");
      this.dialogRef.close(true);
    })
  }

  ngOnInit(): void {
    this.allocateSlotData = this.data;
    this.towerId = this.allocateSlotData.apartmentBlockId;
    if(this.allocateSlotData.slotMainTypeId == 299) {
      //unit owned Tower
      let towerParams = {
        apartmentId: this.sessionService.apartmentId
      }
      this.apartmentService.getApartmentBlockByApartmentId(towerParams).subscribe((res: any) => {
        this.towerList = res;
        if(this.towerId) {  //Edit Mode
          this.getUnits('editMode'); 
        }
      },
      error => {
        console.log(error);
      });
    } else  {
      //community Owned staff
      let staffParms = {
        apartmentId: this.sessionService.apartmentId,
        roleTypeIds: this.sessionService.roleTypeId
      }
      this.staffService.getAllStaffs(staffParms).subscribe((res:any) => {
        if(res.length) {
          res.forEach((ele)=>{
            ele.customLabel = `${ele.staffName}, ${ele.roleName} - ${ele.staffCategoryName}`;
          })
          this.staffsList = res;
        }
      },error => {
        console.log(error)
      });
    }
  }
}
