import { Component, OnInit, ViewChild } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { ParkingService } from 'src/app/api/controllers/Parking';
import { SessionService } from 'src/app/core/session/session.service';
import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-parking-aa-unit-to-unit-allocation',
  templateUrl: './parking-aa-unit-to-unit-allocation.component.html',
  styleUrls: ['./parking-aa-unit-to-unit-allocation.component.scss']
})
export class ParkingAaUnitToUnitAllocationComponent implements OnInit {
  unitAllocationHeader:any
  unitAllocationData:any;
  gridSourceData:any;
  isDataLoaded:boolean = true;
  totalItems:number = 0;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(private parkingService : ParkingService, private sessionService:SessionService) { }

  getUnitAvailability() {
    this.isDataLoaded = true;
    let params = {
      AppartmantId: this.sessionService.apartmentId,
      ApartmentBlockId: null,
      ParkingSlotId: null,
      StartDate: null,
      EndDate: null,
      StartTime: null,
      EndTime: null,
      SlotMainTypeId:299, //UnitOwnedId
      SlotTypeId: null
    };
    this.parkingService.getAllRentedParkingAllocation(params).subscribe((res:any)=>{
      if(res.length > 0) {
        this.totalItems  = res.length;
        this.gridSourceData = {
          localdata: res.reverse(),
          datatype: "array"
        }
        this.unitAllocationData = new jqx.dataAdapter(this.gridSourceData);
      }
      this.isDataLoaded = false;
    })
  }

  ngOnInit() {
    this.getUnitAvailability();
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.unitAllocationHeader = [{
      text: 'SLOT Name',
      datafield: 'slotName',
      width: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'SLOT Type',
      datafield: 'slotType',
      minwidth: 130,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer   
     },{
      text: 'Perm.Owner Unit',
      datafield: 'ownerUnit',
      minwidth: 130,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'Perm.Owner Name',
      datafield: 'permOwner',
      minwidth: 130,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'Temp.Owner Unit',
      datafield: 'permr',
      minwidth: 130,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'Temp.Owner Name',
      datafield: 'per',
      minwidth: 130,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'START DATE',
      datafield: 'startDate',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'START TIME',
      datafield: 'startTime',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value,'HH:mm:ss').format('hh:mm A')+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'END DATE',
      datafield: 'endDate',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'END TIME',
      datafield: 'endTime',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value,'HH:mm:ss').format('hh:mm A')+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'Price',
      datafield: 'totalFee',
      width: 100,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
    }];
  }


  searchList(){
    let searchParam = {
      AppartmantId: 2,
      ApartmentBlockId: 5,
      ParkingSlotId: null,
      StartDate: "",
      EndDate: "",
      StartTime: "",
      EndTime: "",
    };
    this.parkingService.getAllRentedParkingAllocation(searchParam).subscribe(
      resp =>{
      },
        error =>{

        }
    )
  }

}
