import { Component, OnInit, ViewChild } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-parking-aa-community-to-others',
  templateUrl: './parking-aa-community-to-others.component.html',
  styleUrls: ['./parking-aa-community-to-others.component.scss']
})
export class ParkingAaCommunityToOthersComponent implements OnInit {
  unitAllocationHeader:any
  unitAllocationData:any;
  gridSourceData:any;
  //isDataLoaded:boolean = true;
  totalItems:number = 0;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor() { }

  ngOnInit() {
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }

    this.unitAllocationHeader = [{
      text: 'SLOT Name',
      datafield: 'slotName',
      width: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'SLOT Type',
      datafield: 'slotType',
      minwidth: 130,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer   
     },{
      text: 'Perm.Owner Unit',
      datafield: 'ownerUnit',
      minwidth: 130,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'Perm.Owner Name',
      datafield: 'permOwner',
      minwidth: 130,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'Temp.Owner Unit',
      datafield: 'permr',
      minwidth: 130,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'Temp.Owner Name',
      datafield: 'per',
      minwidth: 130,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
     },{
      text: 'START DATE',
      datafield: 'startDate',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'START TIME',
      datafield: 'startTime',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value,'HH:mm:ss').format('hh:mm A')+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'END DATE',
      datafield: 'endDate',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'END TIME',
      datafield: 'endTime',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value,'HH:mm:ss').format('hh:mm A')+'</div>'
      },
      minwidth: 100,
      renderer: columnrenderer
     },{
      text: 'Price',
      datafield: 'totalFee',
      width: 100,
      cellsrenderer:cellsrenderer,
      renderer: columnrenderer
    }];
  }
}
