import { Component, OnInit, ViewChild, HostListener, Injector } from '@angular/core';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { ModalService } from 'src/app/shared/services/modal.service';
import { ParkingService } from 'src/app/api/controllers/Parking';

import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-parking-slot-rate',
  templateUrl: './parking-slot-rate.component.html',
  styleUrls: ['./parking-slot-rate.component.scss']
})
export class ParkingSlotRateComponent implements OnInit {
  public isCreateSlot: boolean = true;
  public slotRateData: Array<any> = [];
  public slotRateHeader: any;
  public isShowCreateSlot:boolean=false;
  public filterSlotRate="";
  isDataLoaded:boolean = true;
  totalItems:number = 0;
  public modalService:any;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  public slotRateParam = {
    "id": 0,
    "name": "",
    "apartmentId": this.sessionService.apartmentId,
    "fee": 0,
    "isActive": true,
    "feeIntervalId": 1,
    "canCancelledBefore": 5,
    "insertedBy": 0,
    "insertedOn": "2020-05-15T17:24:51.028Z",
    "updatedBy": 0,
    "updatedOn": "2020-05-15T17:24:51.028Z",
    "finePercent": 1,
    "feeCalcultionType": ""
  }

  
  @HostListener('window:onEditSlot', ['$event.detail'])
  onEditTicket(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    this.isCreateSlot = false;
    this.isShowCreateSlot = true;
    for (const key in dataRecord) {
      this.slotRateParam[key] = dataRecord[key];
    }
  }

  
  @HostListener('window:onDeleteSlot', ['$event.detail'])
  onDeleteSlot(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let slotId = dataRecord.id
    this.modalService.showConfirmModal(slotId);
  }

  constructor(private parkingService: ParkingService,
    private sessionService: SessionService,
    private injector: Injector,
    private sharedService: SharedService) { 
    this.modalService = this.injector.get(ModalService);
    }

  ngOnInit(): void {
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    // delete item
    this.sharedService.unitlistdeleteindexcast.subscribe(item => {
      if (item != null) {
        var params = {
          parkingSlotId: item,
          deleteBy: parseInt(this.sessionService.userId)
        }
        this.parkingService.deleteParkingSlotType(params).subscribe((res: any) => {
          // if (res.message) {
            this.getSlotRateType();
            this.sharedService.setUnitListDeleteIndex(null);
          // }
        });
      }
    });


    this.slotRateHeader = [{
      text: 's no',
      width: 100,
      cellsrenderer: (index: number) => {
        return `<div class="jqx-custom-inner-cell">${index+1}</div>`;
      },
      renderer: columnrenderer
    }, {
      text: 'slot rate type',
      datafield: 'name',
      minwidth: 100,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'slot rate',
      datafield: 'fee',
      cellsrenderer: cellsrenderer,
      minwidth: 100,
      renderer: columnrenderer
    }, {
      text: 'calculation type',
      datafield: 'feeCalcultionType',
      cellsrenderer: cellsrenderer,
      minwidth: 100,
      renderer: columnrenderer
    }, {
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      width: 120,
      cellsrenderer: (row): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="editSlot(' + row + ')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="deleteSlot(' + row + ')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a></div>'
      },
      renderer: columnrenderer
    }];
    this.getSlotRateType();
  }

  getSlotRateType() {
    this.isDataLoaded = true;
    let getParam ={
      ApartmantId:this.sessionService.apartmentId
    }
    this.parkingService.getParkingAllocationType(getParam).subscribe((resp:any) =>{
      if(resp.length > 0) {
        this.totalItems = resp.length;
        var rawSlotRateType = resp.filter(data => {
          return data.isActive
        });
        let arrangeSlotRawData = {
          localdata: rawSlotRateType,
          datatype: "array"
        }
        this.slotRateData = new jqx.dataAdapter(arrangeSlotRawData);
      }
      this.isDataLoaded = false;
    })
  }

  searchSlotRate() {
    if(this.filterSlotRate != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.filterSlotRate;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.slotRateHeader.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }


  slotRateType() {
    if (this.isCreateSlot) {
      this.slotRateParam.insertedBy = parseInt(this.sessionService.userId);
      this.slotRateParam.insertedOn = _moment().toISOString();
      let addParam ={
        parkingAllocationTypeDto:this.slotRateParam
      }
      this.parkingService.upsertParkingSlotType(addParam).subscribe(
        resp => {
          this.sharedService.setAlertMessage("Slot Rate Type Created Successfully");
          this.isShowCreateSlot = !this.isShowCreateSlot;
          this.getSlotRateType();
        },
        error => {

        }
      )
    } else {
      this.slotRateParam.updatedBy = parseInt(this.sessionService.userId);
      this.slotRateParam.updatedOn = _moment().toISOString();
      let updateParam ={
        parkingAllocationTypeDto:this.slotRateParam
      }
      this.parkingService.upsertParkingSlotType(updateParam).subscribe(
        resp => {
          this.sharedService.setAlertMessage("Slot Rate Type Updated Successfully");
          this.isShowCreateSlot = !this.isShowCreateSlot;
          this.getSlotRateType();
        },
        error => {

        }
      )
    }
  }
}


function deleteSlot(row){
  var event = new CustomEvent('onDeleteSlot', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).deleteSlot = deleteSlot;


function editSlot(row){
  var event = new CustomEvent('onEditSlot', {
    detail:{
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editSlot = editSlot;
