import { Component, OnInit } from '@angular/core';
import { Subject, Observable, of, throwError, BehaviorSubject } from 'rxjs';
import { debounceTime, distinctUntilChanged, tap, filter, map, switchMap, catchError} from 'rxjs/operators';
import { ParkingService } from 'src/app/api/controllers/Parking';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { Router, ActivatedRoute } from '@angular/router';

import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-create-availablity',
  templateUrl: './create-availablity.component.html',
  styleUrls: ['./create-availablity.component.scss']
})
export class CreateAvailablityComponent implements OnInit {
  slotSubject = new BehaviorSubject<string>('');
  slotData$:any;
  parkingSlotData:any = {};
  parkingSlotList:any;
  type:string;

  timeZone:any;

  constructor( private parkingService: ParkingService, private sessionService:SessionService,
    private sharedService: SharedService,private router : Router,
    private activateRouter: ActivatedRoute) { }

  onSelectSlot(event) {
    let selectedValue = event.option.value;
    this.parkingSlotData.displaySlotNumber =  `${selectedValue.parkingSlotNo} location ${selectedValue.slotTypeName}(${selectedValue.permOwner} ${selectedValue.apartmentBlockNumber} ${selectedValue.apartmentBlockUnitNumber})`;
    this.parkingSlotData.parkingSlotNo = selectedValue.parkingSlotNo;  //Slot Name
    this.parkingSlotData.parkingSlotID = selectedValue.parkingSlotID;  // Slot Name Id
    this.parkingSlotData.slotTypeName = selectedValue.slotTypeName;   
    this.parkingSlotData.permOwner = selectedValue.permOwner;
    this.parkingSlotData.parkingSlotBlockNumber =  selectedValue.parkingSlotBlockNumber //Slot Location
    this.parkingSlotData.parkingSlotBlockID =  selectedValue.parkingSlotBlockID //Slot Location Id
  }

  submitParkingSlot() {
    let entity = {
      "id": 0,
      "parkingId": this.parkingSlotData.parkingSlotID,
      "startDate": this.parkingSlotData.startDate,
      "endDate":  this.parkingSlotData.endDate,
      "startTime": moment(this.parkingSlotData.startTime).format(this.timeZone.time24),
      "endTime": moment(this.parkingSlotData.endTime).format(this.timeZone.time24),
      "isActive": true,
      "insertedBy": parseInt(this.sessionService.userId),
      "insertedOn": _moment().toISOString(),
      "updatedBy": null,
      "updatedOn": null,
      "slotMainTypeId": this.type == 'unit' ? 299 : 300,
      "slotTypeId": null,
      "parkingSlotBlockNumber": null,
      "parkingSlotLocation":  null,
      "staffId": null,
      "staffName": null,
      "permOwnerUserId": null,
      "permOwner": null,
      "tempOwnerUserId": null,
      "tempOwner": null
    }
    let parking = {
      availabelParking : entity
    }
    this.parkingService.addAvailableParking(parking).subscribe((res:any)=>{
      if(!isNaN(res.message)) {
        this.sharedService.setAlertMessage("Parking Slot Created successfully");
        this.router.navigate(['/ams/parking-management/set-parking-availablity/unit-parking-availablity'])
      } else if(res.message) {
        this.sharedService.setAlertMessage(res.message);
      } else {
        this.sharedService.setAlertMessage(res.errorMessage);
      }
    })
  }

  ngOnInit() {

    this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

    this.activateRouter.params.subscribe(param=>this.type = param['type']);
    this.slotData$ = this.slotSubject.pipe(
      debounceTime(300),
      distinctUntilChanged(),
      filter(data=>data.length > 2),
      switchMap(term=>{
        let entity = {
          ApartmentID: this.sessionService.apartmentId,
          KeywordSlotNo: term,
          SlotMainTypeId : this.type == 'unit' ? 299 : 300,
        }
        return this.parkingService.getParkingSlotNoByKeyword(entity)
      }),
      catchError(e=>{
        if(e) {
          return throwError(e);
        }
      })
    )
    this.slotData$.subscribe(res=>{
      if(res.length > 0) {
        this.parkingSlotList = res;
      }
    },
    (error)=>{
    }
    )
  }
}
